import "./styles.css";
import { useState, useEffect } from "react";
import { TSV } from "tsv";

const parseToVacationAndSickness = (data, year) =>
  data
    ? data.reduce(
        (acc, curr) => {
          const addType = (type) =>
            curr["Type"].toLowerCase().includes(type) ? curr["Requested"] : 0;
          const added = {
            vacation: acc.vacation + addType("vacation"),
            sickness: acc.sickness + addType("sickness")
          };
          if (year) {
            return curr["Date"]?.includes?.(year) ? added : acc;
          }
          return added;
        },
        {
          vacation: 0,
          sickness: 0
        }
      )
    : "";

export default function App() {
  const [stats, setStats] = useState("");
  const [list, setList] = useState("");
  const [error, setError] = useState(false);
  const [year, setYear] = useState(false);

  useEffect(() => {
    let timeout = 0;
    if (error) {
      timeout = setTimeout(() => {
        setError(false);
      }, 10000);
    }
    return () => clearTimeout(timeout);
  }, [error]);

  useEffect(() => {
    console.log(list);
    const newList = parseToVacationAndSickness(list, year);
    console.log(newList);
    setStats(newList);
  }, [list, year]);

  return (
    <div class="App">
      <h2>Usage</h2>
      <p>
        <ol>
          <li>
            Go to <a href="https://wd3.myworkday.com/klarna">workday</a>
          </li>
          <li>Click on "Absence"</li>
          <li>Click on "My Absence"</li>
          <li>
            On the top-right above the table click the second icon ("Export to
            worksheets")
          </li>
          <li>Mark the table and copy it, then paste it here ðŸ‘‡</li>
        </ol>
      </p>
      <textarea
        id="list-textarea"
        onChange={(ev) => {
          console.log(ev.target.value?.indexOf("Absence Request"));
          const raw = ev.target.value?.indexOf("Absence Request")
            ? ev.target.value
            : ev.target.value.split("\n").slice(1).join("\n");
          console.log(raw);
          setList(TSV.parse(raw));
        }}
        cols={100}
        rows={20}
      ></textarea>
      {error && (
        <>
          <p style={{ color: "red" }}>
            This file doesn't look right. .csv file must contain merchant_name
            and a column with 'brand' in its title.
          </p>
          <button onClick={() => setError(false)}>Okay</button>
          <br />
        </>
      )}
      <h3>Enter the year you're interested in:</h3>
      <input onChange={(ev) => setYear(ev.target.value)} />
      <br />
      <p>Your vacation days: {stats.vacation || 0}</p>
      <p>Your sick days: {stats.sickness || 0}</p>
      <br />
    </div>
  );
}
